html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 400;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  background-color: white;
  overflow: auto;
  position: relative;
}

.container {
  max-width: 1600px;
  padding: 0 20px;
  margin: 0 auto;
}

//colors
$primary: #323232;
$aqua: #22bac9;
$blue: #065c9e;
.register-btn {
  color: $primary;
  text-transform: uppercase;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 30px;
  min-width: 222px;
  border: 1px solid $primary;
  text-align: center;
  padding: 5px 40px;
  transition: 0.3s all;
  background: #fff;
  cursor: pointer;
  &:hover {
    color: #fff;
    background: $primary;
  }
}

.register-btn-black {
  @extend .register-btn;
  color: #fff;
  background: $primary;
  display: inline-block;
  &:hover {
    color: $primary;
    background: #fff;
  }
}

.learn-more-btn {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 28px;
  text-transform: uppercase;

  color: #202020;
  img {
    margin-right: 20px;
  }
}

.go-back {
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 28px;
  text-transform: uppercase;

  color: #505050;

  display: flex;
  align-items: center;
  transition: 0.3s all;
  &:hover {
    color: $blue;
  }
  img {
    width: 30px;
    margin-right: 20px;
  }
}

.clear-button {
  @extend .register-btn;
  width: 100%;
  max-width: 222px;
  display: inline-block;
  margin-top: 10px;
}

.main-navigation {
  width: 100%;
}


.load-more {
    @extend .register-btn;
    margin: 0 auto;
    max-width: 222px;
    text-align: center;
    justify-content: center;
    cursor: pointer;
    align-self: center;
    position: absolute;
    bottom: -100px;
    left: 50%;
    transform: translateX(-50%);
}

